.hero {
	padding: 120px 0 80px;
	min-height: 100vh;
	height: 100%;
	display: flex;
	align-items: center;
	position: relative;
	@include sm {
		padding: 75px 0 80px;
		min-height: 0; }
	&__inner {
		position: relative; }
	&__content {
 }		// width: 100%
	&__sphere {
		position: absolute;
		&_sm {
			top: -148px;
			left: 152px;
			transform: translateX(calc(1px - (100vw - 1340px) / 2 - 171px - 148px - 80px));
			@include lg {
				display: none; } }
		&_md {
			top: 100%;
			right: 325px;
			margin-top: -12px;
			transform: translateX(calc(1px - (100vw - 1340px) / 2 - 717px - 148px - 120px));
			@include lg {
				top: auto;
				bottom: 8.4vw;
				right: 328px;
				margin-top: 0; }
			@include sm {
				display: none; } }
		&_lg {
			top: -51px;
			right: 190px;
			transform: translateX(calc(1px - (100vw - 1340px) / 2 - 761px - 148px - 211px));
			@include lg {
				right: 152px;
				top: 0.5vw; }
			@include md {
				right: 0px;
				top: 75px; } } }
	&__bg {
		position: absolute;
		width: calc((100% - 1340px)/2 + 1340px - 510px);
		overflow: hidden;
		top: 0;
		right: 0;
		bottom: 0;
		@include sm {
			width: 50%; } }
	&__content {
		position: relative; }
	&__title {
		line-height: 1.2;
		margin-left: -.05em;
		@include lg {
			margin-left: -.08em; } }
	&__subtitle {
		margin-top: -30px;
		@include lg {
			margin-top: -20px; }
		@include md {
			margin-top: -10px; }
		@include sm {
			padding-right: 80px;
			margin-top: 3px; } }
	&__splitter {
		margin: 67px 0;
		overflow: hidden;
		@include lg {
			margin: 2.7vw 0; }
		@include md {
			margin: 21px 0; } }
	&__line {
		display: block;
		width: 1px;
		height: 30px;
		background-color: $greyMid;
 }		// transform: translateY(-100%)
	&__text {
		max-width: 550px;
		@include sm {
			line-height: 1.5; } }
	&__footer {
		position: absolute;
		left: 0;
		bottom: 0;
		right: 0;
		padding-bottom: 58px;
		@include lg {
			padding-bottom: 30px; }
		@include sm {
			padding-bottom: 8px; }
		&-wrap {
			display: flex;
			justify-content: space-between;
			align-items: center; } }
	&__btn {
		&:not(:first-child) {
			margin-left: 15px; } }
	&__link {
		display: block;
		color: $greyHigh;
		text-decoration: none;
		transition: color .3s;
		@include hover {
			color: darken($greyHigh, 10%); }
		@include sm {
			display: none; } } }
