//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$proxima: 'ProximaNova';
$averta: 'Averta';
$montserrat: 'Montserrat';

@font-face {
  @include fontdefault(normal, 400, $proxima);
  @include fontpath('ProximaNova-Regular', 'ProximaNova-Regular', ProximaNova-Regular); }

@font-face {
  @include fontdefault(normal, 600, $proxima);
  @include fontpath('ProximaNova-Semibold', 'ProximaNova-Semibold', ProximaNova-Semibold); }

@font-face {
  @include fontdefault(normal, 700, $proxima);
  @include fontpath('ProximaNova-Bold', 'ProximaNova-Bold', ProximaNova-Bold); }

@font-face {
  @include fontdefault(normal, 300, $averta);
  @include fontpath('Averta-Light', 'Averta-Light', Averta-Light); }

@font-face {
  @include fontdefault(normal, 400, $averta);
  @include fontpath('Averta-Regular', 'Averta-Regular', Averta-Regular); }

@font-face {
  @include fontdefault(normal, 900, $averta);
  @include fontpath('Averta-Black', 'Averta-Black', Averta-Black); }

@font-face {
  @include fontdefault(normal, 400, 'Averta-Bold');
  @include fontpath('Averta-Bold', 'Averta-Bold', Averta-Bold); }

