.btn { padding: 11px 26px; font-family: "ProximaNova", Helvetica Neue, Helvetica, Arial, sans-serif; color: #817D8E; font-size: 18px; font-weight: 400; line-height: 1; vertical-align: top; display: inline-block; border-radius: 20px; background-color: rgba(152, 149, 162, 0.1); transition: background-color .3s, color .3s; }

.no-touch .btn:hover { color: #3E3852; background-color: rgba(152, 149, 162, 0.2); }

