.filter-btn { background-color: transparent; font-family: "ProximaNova", Helvetica Neue, Helvetica, Arial, sans-serif; font-size: 18px; line-height: 1.667; color: #9F9CA9; }

.filter-btn.is-active { color: #F03454; }

.anim-btn { position: relative; display: inline-flex; align-items: center; font-family: "ProximaNova", Helvetica Neue, Helvetica, Arial, sans-serif; line-height: 1.667; font-size: 18px; color: #817D8E; text-decoration: none; background-color: transparent; }

.no-touch .anim-btn:hover .anim-btn__text { transform: translateX(24px); }

.no-touch .anim-btn:hover .anim-btn__text-wrapper:before { animation: shadeAnimIn 0.5s cubic-bezier(0.8, 0, 0.2, 1) forwards; }

.no-touch .anim-btn:hover .anim-btn__text-wrapper:after { transform: translate(0, -50%); }

.no-touch .anim-btn:hover .anim-btn__line { transform: translateX(-50%); }

.no-touch .anim-btn:hover .anim-btn__line-wrapper { transform: translateX(-10px); }

.no-touch .anim-btn_no-scroll-anim:hover .anim-btn__line { transform: none; }

.anim-btn__text { position: relative; display: block; vertical-align: top; transition: transform 0.5s; }

.anim-btn__text-wrapper { position: relative; display: inline-block; overflow: hidden; margin-left: 40px; padding-right: 54px; }

.anim-btn_no-scroll-anim .anim-btn__text-wrapper { margin-left: 32px; }

.anim-btn_pull-left .anim-btn__text-wrapper { margin-left: 0; }

.anim-btn__text-wrapper:after { content: ''; position: absolute; top: 50%; right: 0; margin-top: 1px; transform: translate(-32px, -50%); width: 0px; height: 0px; border-style: solid; border-width: 5px 0 5px 6px; border-color: transparent transparent transparent #F03454; transition: transform 0.5s; }

.anim-btn__text-wrapper:before { content: ''; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(152, 149, 162, 0.1); transform: translateX(103%); animation: shadeAnimOut 0.5s cubic-bezier(0.8, 0, 0.2, 1) forwards; }

.anim-btn__line-wrapper { width: 40px; overflow: hidden; display: inline-block; vertical-align: middle; transition: transform 0.3s; }

.anim-btn__line { height: 1px; background-color: #9F9CA9; display: block; }

@keyframes shadeAnimIn { 0% { transform: translateX(-103%); }
  2% { transform: translateX(-103%); }
  100% { transform: translateX(-50%); } }

@keyframes shadeAnimOut { 0% { transform: translateX(-50%); }
  100% { transform: translateX(103%); } }

