.menu-item:before { counter-increment: number; content: "0" counter(number); position: absolute; left: 0; top: 50%; transform: translateY(-50%); font-size: 14px; }

@media only screen and (max-width: 767px) { .header .menu-item:before { display: none; } }

.menu-item__line-wrap { position: absolute; top: 50%; right: 0; width: 40px; height: 1px; }

