.clients {
  // height: 100vh
  padding: 356px 0 170px;
  @include lg {
    padding: 145px 0 115px; }
  @include sm {
    padding: 40px 0 13px; }
  // +vertical
  &__inner {
    text-align: center;
    @include sm {
      text-align: left; }
    // display: inline-block
 }    // vertical-align: middle
  &__text {
    margin-bottom: 76px;
    @include sm {
      margin-bottom: 42px; } }
  &__title {
    margin-bottom: 7px;
    @include sm {
      margin-bottom: 7px; } }
  &__descr {
    line-height: 1.4;
    font-family: $proxima;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.667;
    color: $greyHigh;
    @include sm {
      font-size: 14px;
      letter-spacing: -0.01em;
      line-height: 1.428; } }
  &__logos {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -45px;
    @include md {
      margin: 0 -15px; }
    @include sm {
      justify-content: space-between;
      align-items: flex-start; } }
  &__logo-wrapper {
    padding: 0 45px;
    position: relative;
    @include md {
      padding: 0 15px; }
    @include sm {
      margin-bottom: 30px; }
    img {
      display: block;
      max-width: 100%;
      @include sm {
        max-height: 40px; } } }
  &__line {
    margin-top: 166px;
    text-align: center;
    @include lg {
      margin-top: 101px; }
    @include sm {
      display: none; } } }

.vertical-line {
  display: inline-block;
  width: 1px;
  height: 60px;
  background-color: $greyMid; }
