.footer {
  display: flex;
  align-items: center;
  bottom: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background-color: rgba(152, 149, 162, 0.1);
  z-index: 8;
  overflow: hidden;
  position: fixed;
  &__mask {
    @include coverdiv;
    z-index: 20;
    pointer-events: none;
    background-color: $black; }
  &__inner {
    position: relative;
    display: block;
    padding-top: 100px;
    padding-bottom: 100px;
    @include lg {
      padding-top: 100px;
      padding-bottom: 100px; }
    @include md {
      padding-top: 100px;
      padding-bottom: 100px; } }
  &__link {
    height: 100vh;
    width: 100vw;
    @include vcenter;
    z-index: 3;
    cursor: url('../img/cursor.svg') 50 50, pointer; }
  &__title {
    padding: 3.9vw 0 4.7vw;
    text-align: center;
    @include sm {
      padding: 60px 0 70px; } }
  &__wrapper {
    max-width: 566px;
    margin: 0 auto;
    position: relative;
    @include sm {
      text-align: center; }
    &_left {
      text-align: left; }
    &_right {
      text-align: right; } }
  &__btn-wrapper {
    position: relative;
    margin-right: -30px;
    z-index: 5;
    height: 0; }
  &__info {
    position: absolute;
    width: 100%;
    bottom: 0;
    // z-index: 3
    .social-links {
      cursor: pointer; } }
  &__sphere {
    position: absolute;
    @include sm {
      display: none; }
    &_lg {
      top: 4px;
      right: 148px;
      @include lg {
        top: 19px;
        right: 118px; } }
    &_sm-left-center {
      left: 100px;
      top: 50%;
      transform: translateY(-50%);
      @include lg {
        left: 85px;
        top: 98px;
        transform: none; }
      @include md {
        left: 20px; } } } }

.footer-info {
  padding: 45px 0 50px;
  @include lg {
    padding: 30px 0; }
  @include sm {
    padding: 15px 0; }
  &__inner {
    display: flex;
    justify-content: space-between;
    // align-items: center
    @include sm {
      justify-content: center; } }
  &__social {
    position: relative;
    z-index: 5;
    @include sm {
      display: none; } }
  &__copyright {
    position: relative;
    z-index: 5; } }

.social-links {
  display: flex;
  align-items: center;
  margin: 0 -19px;
  font-size: 0;
  &__item {
    display: block;
    padding: 0 19px; }
  &__link {
    // font-size: 30px
    color: $social-icon-color;
    transition: color 0.3s;
    @include hover {
      color: $greyMid; }
    .icon {
      font-size: 20px; }
    .icon-behance {
      font-size: 17px; } } }
