.header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	padding-top: 61px;
	z-index: 100;
	@include lg {
		padding-top: 30px; }
	@include sm {
		padding-top: 18px; }
	&__inner {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		@include sm {
			align-items: flex-start; } }
	&__left {
		margin-top: 9px; }
	&__menu {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 2;
		width: 100%;
		height: 100vh;
		overflow: auto;
		opacity: 0;
		visibility: hidden;
		background-color: $white; }
	&__menu-wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		min-height: 100vh;
		padding: 115px 0;
		display: flex;
		overflow: hidden;
		align-items: center;
		@include sm {
			padding: 60px 0 120px; } }
	&__menu-top {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		padding-top: 61px;
		transform: translateY(-61px);
		opacity: 0;
		@include lg {
			padding-top: 30px; }
		@include sm {
			padding-top: 18px; } }
	&__wrap {
		width: 100%; }
	&__center {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__text {
		padding-right: 20px;
		margin-top: 25px;
		margin-left: -0.3em;
		@include lg {
			margin-top: 0; }
		@include sm {
			display: none; } }
	&__nav {
		max-width: 417px;
		width: 100%;
		@include lg {
			max-width: 329px; }
		@include sm {
			max-width: 100%; } }
	&__right {
		position: relative;
		padding-right: 26px;
		text-align: right;
		min-width: 50%; }
	&__btn {
		margin-right: 39px;
		@include sm {
			display: none; } }
	&__footer {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		transform: translateY(35px);
		@include sm {
			padding: 0 0 59px 30px; } }
	&__btn-menu {
		position: fixed;
		display: inline-block;
		z-index: 100; } }
.logo {
	width: 120px;
	display: inline-block;
	@include sm {
		width: 85px; }
	img {
		max-width: 100%;
		display: block; } }

.btn-menu {
	position: relative;
	z-index: 100;
	width: 26px;
	height: 40px;
	vertical-align: top;
	background-color: transparent;
	display: inline-block;
	// .header &
	// 	position: fixed
	&:before {
		position: absolute;
		content: "";
		top: 0;
		left: -7px;
		right: -7px;
		bottom: 0; }
	&__line {
		position: absolute;
		left: 0;
		width: 25px;
		height: 2px;
		transition: transform .3s;
		transform-origin: center;
		background-color: $dark;
		display: block;
		&:first-child {
			top: 14px;
			.btn-menu.is-open & {
				transform: translate(0,5px) rotate(45deg); } }
		&:last-child {
			top: 24px;
			.btn-menu.is-open & {
				transform: translate(0,-5px) rotate(-45deg); } } } }
.nav {
	&__list {}
	&__item {}
	&__link {} }
