body {
	background: $white;
	line-height: 1.4;
	font-family: $proxima;
	font-weight: 400;
	font-size: 18px;
	line-height: 1.667;
	color: $greyHigh;
	padding-bottom: 100vh;
	min-width: 320px;
	@include smooth-font;
	@include sm {
		font-size: 14px; } }
html {
	&.is-overflow-hidden {
		overflow-x: hidden;
		overflow-y: scroll;
		body {
			overflow: hidden;
			height: 100vh; } } }

.out {
	position: relative; }

.section-bg {
	position: absolute;
	top: 0;
	bottom: 0;
	width: 100%;
	background-color: rgba(152, 149, 162, 0.1); }

.main-wrapper {
	position: relative;
	z-index: 9;
	background-color: $white; }

.sphere {
	opacity: 0.3;
	img {
		display: block;
		max-width: 100%; }
	&_lg {
		width: 211px;
		height: 188px;
		@include lg {
			width: 148px;
			height: 132px; }
		@include sm {
			width: 95px;
			height: 85px; } }
	&_md {
		width: 120px;
		height: 107px;
		@include lg {
			width: 85px;
			height: 75px; }
		@include sm {
			display: none; } }
	&_sm {
		width: 80px;
		height: 71px;
		@include lg {
			width: 53px;
			height: 47px; }
		@include sm {
			display: none; } } }

.mob-show {
	display: none !important;
	@include sm {
		display: block !important; } }

.mob-hide {
	@include sm {
		display: none !important; } }
